
































type RecordItem = {
  type: String;
  remark: String;
  amount: number;
  tag: String;
  date: string;
};
import dayjs from "dayjs";
import { Component, Vue } from "vue-property-decorator";
import DatePicker from "@/components/DatePicker.vue";
import { Empty } from "vant";
import Header from "@/components/Header.vue";
@Component({
  components: {
    DatePicker,
    Header,
    vanEmpty: Empty,
  },
  beforeRouteLeave(to: any, from: any, next: any) {
    //判断是否缓存detail页面
    from.meta.keepAlive = true;
    next();
  },
})
export default class Statistics extends Vue {
  recordList = this.$store.state.recordList;

  empty: Boolean = true;

  date: Year_Month = {
    year: dayjs(new Date()).year(),
    month: dayjs(new Date()).month() + 1,
  };

  //计算每天的收入支出
  sum(item: RecordItem[]) {
    let income: number = 0;
    let pay: number = 0;
    item.forEach((i) => {
      if (i.type === "收入") {
        income += i.amount;
      } else {
        pay += i.amount;
      }
    });
    return [income, pay];
  }
  //按日期分组渲染
  get hashtable() {
    const hashtable: { [key: string]: RecordItem[] } = {};
    //按当前月份过滤
    const newList = this.recordList.filter((item: RecordItem) => {
      return (
        dayjs(item.date).year() === this.date.year &&
        dayjs(item.date).month() + 1 === this.date.month
      );
    });
    this.empty = newList.length === 0 ? true : false;
    //按日期排序分组
    newList
      .sort((a: RecordItem, b: RecordItem) => {
        return dayjs(b.date).valueOf() - dayjs(a.date).valueOf();
      })
      .forEach((item: RecordItem) => {
        const key = dayjs(item.date)
          .format("MM月DD日")
          .toString();
        hashtable[key] = hashtable[key] || [];
        hashtable[key].push(item);
      });

    return hashtable;
  }
}
