























import dayjs from "dayjs";
import { Component, Vue, Prop } from "vue-property-decorator";
import DatePicker from "@/components/DatePicker.vue";

@Component({
  components: {
    DatePicker,
  },
})
export default class extends Vue {
  //   date = new Date();
  @Prop({ type: Object, required: true }) date: Year_Month | undefined;

  //computed
  get pay() {
    const amount = this.getMoney("支出");
    this.$emit("pay", amount);
    return amount;
  }
  get income() {
    const amount = this.getMoney("收入");
    this.$emit("income", amount);
    return amount;
  }
  //Methods
  // 计算当月总收入,支出
  getMoney(type: string) {
    const recordList = this.$store.state.recordList;
    return recordList
      .filter((item: RecordItem) => {
        return (
          dayjs(item.date).year() === this.date!.year &&
          dayjs(item.date).month() + 1 === this.date!.month
        );
      })
      .filter((item: RecordItem) => {
        return item.type === type;
      })
      .reduce((total: number, item: RecordItem) => {
        return total + item.amount;
      }, 0);
  }
  //获取时间选择框的时间
  updateDate(value: Date) {
    const date: Year_Month = {
      year: dayjs(value).year(),
      month: dayjs(value).month() + 1,
    };
    //月份无更改 中断向上传播 无需重新计算income pay
    if (JSON.stringify(date) === JSON.stringify(this.date)) {
      return;
    }
    this.$emit("update:date", date);
  }
  //点击显示时间选择框
  showPopup() {
    (this.$refs.DatePicker as DatePicker).showPopup();
  }
}
